.grid-item-rooms {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    height: 100%;
    /* border-radius: 9px; */
    box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.1);
