    padding: 0.25rem 0.5rem;
}





/* Hover Effects */
.grid-cell {
    position: relative;
    border-radius: 4px;
    transition: all 0.3s ease;
}

.hover-info {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.3);
    color: white;
    padding: 5px 10px;
    border-radius: 4px;
    font-size: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    pointer-events: none;
    z-index: 10;
}

.grid-cell:hover .hover-info {
    opacity: 1;
}


