.menu-trigger {
    background-color: #1976d2;
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 6px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 0.875rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    width: 90%;
    margin: 0 auto;
}

.quick-nav-menu {
    position: absolute;
    top: 100%;
    left: 5%;
    margin-top: 8px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
    padding: 8px;
    width: 90%;
    display: none;
}

.quick-nav-menu.open {
    display: block;
}

.quick-nav-button {
    width: 100%;
    padding: 8px 16px;
    margin: 4px 0;
    border: none;
    background: none;
    text-align: left;
    display: flex;
    align-items: center;
    gap: 8px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.2s;
}

.quick-nav-button:hover {
    background-color: #f5f5f5;
}

.search-dropdown {
    position: absolute;
    left: 100%;
    top: 0;
    margin-left: 8px;
    width: 350px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

.search-tabs {
    display: flex;
    padding: 8px;
    gap: 8px;
    background-color: #f5f5f5;
    border-bottom: 1px solid #e0e0e0;
}

.search-tab-button {
    flex: 1;
    padding: 8px 16px;
    border-radius: 6px;
    font-size: 0.875rem;
    font-weight: 500;
    transition: all 0.2s ease;
}

.search-tab-button.active {
    background-color: #1976d2;
    color: white;
}

.search-tab-button:not(.active) {
    background-color: #ffffff;
    color: #666;
}

.search-tab-button:not(.active):hover {
    background-color: #f0f0f0;
}

